import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnExport from 'components/common/button/BtnExport';
import { Card, Row, Col, Button } from 'antd';
import { CartHeadAction, TableData } from 'components/common';
import { numberToMoney } from 'utils/formatUtil';
import { getReceipts } from 'services/receiptsAndExpensesStatistic';
import FormSearchStudent from './FormSearchStudent';
import './style.scss';

class ReceiptsAndExpensesStatisticPage extends Component {
  state = {
    reload: 0,
    paramSearchClass: {},
  };

  dataFormSeach = data => {
    this.setState({
      paramSearchClass: data,
    });
  };

  render() {
    const { reload, paramSearchClass } = this.state;
    console.log(paramSearchClass);
    const columns = [
      {
        title: 'STT',
        dataIndex: 'index',
        key: 'index',
        align: 'center'
      },
      {
        title: 'Khoản thu',
        dataIndex: 'field1',
        key: 'field1',
        align: 'left'
      },
      {
        title: 'Số tiền phải thu',
        dataIndex: 'field2',
        key: 'field2',
        align: 'right',
        render: value => numberToMoney(value)
      },
      {
        title: 'Số tiền đã thu',
        dataIndex: 'field3',
        key: 'field4',
        align: 'right',
        render: value => numberToMoney(value)
      },
      {
        title: 'Số tiền đã chi',
        dataIndex: 'field4',
        key: 'field4',
        align: 'right',
        render: value => numberToMoney(value)
      },
      {
        title: 'Số tiền còn lại',
        dataIndex: 'field5',
        key: 'field5',
        align: 'right',
        render: value => numberToMoney(value)
      }
    ];

    return (
      <Card title="Báo cáo tổng hợp thu/chi">
        <CartHeadAction>
          <div className="btn-group">
            <BtnExport
              className="btnExport"
              title="Xuất Excel"
            />
            <Button
              className="btnAction btnPrint"
              icon="printer"
              size="large"
            >
              In
            </Button>
          </div>
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <FormSearchStudent dataForm={this.dataFormSeach} />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              // formSearch
              service={getReceipts}
              // paramSearch={paramSearchClass}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ReceiptsAndExpensesStatisticPage />
  </Page>
);



